import { Card, CardContent, Divider, Stack, Typography } from '@mui/material';
import { CardTitleAndValue } from '@components/common/styles';
import React, { useContext } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { cleanUnderscore, commaNotation, notateNumber, prefix } from '@utility/helpers';
import InfoIcon from '@mui/icons-material/Info';
import Tooltip from '@components/Tooltip';
const Grimoire = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    bones,
    upgrades,
    totalUpgradeLevels,
    nextUnlock,
    wraith
  } = state?.account?.grimoire;
  return <>
    <Stack direction={'row'} gap={{
      xs: 1,
      md: 3
    }} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="grimoire.jsx">
      <CardTitleAndValue title={'Total Levels'} value={totalUpgradeLevels} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Next upgrade'} value={<Tooltip title={<Stack gap={1}>
        <Typography sx={{
          fontWeight: 'bold'
        }}>{cleanUnderscore(nextUnlock?.name)}</Typography>
        <Typography>{cleanUnderscore(nextUnlock?.description)}</Typography>
      </Stack>}>
        <Stack direction={'row'} gap={1}>
          {nextUnlock?.unlockLevel}
          <InfoIcon />
        </Stack>
      </Tooltip>} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      {bones?.map((amount, index) => <CardTitleAndValue key={index} value={commaNotation(amount || '0')} title={`Bone ${index + 1}`} icon={`data/Bone${index}_x1.png`} imgStyle={{
        objectPosition: '0 -6px'
      }} />)}
    </Stack>
    <Divider sx={{
      my: {
        xs: 2,
        md: 0
      }
    }} data-sentry-element="Divider" data-sentry-source-file="grimoire.jsx" />
    <Stack direction={'row'} gap={{
      xs: 1,
      md: 3
    }} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="grimoire.jsx">
      <CardTitleAndValue title={'Wraith Max HP'} value={notateNumber(wraith?.hp)} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Damage'} value={notateNumber(wraith?.damage)} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Accuracy'} value={notateNumber(wraith?.accuracy)} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Defence'} value={notateNumber(wraith?.defence)} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Crit Chance *'} value={`${wraith?.critChance}%`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Crit Damage *'} value={`${notateNumber(wraith?.critDamage, 'MultiplierInfo')}x`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
      <CardTitleAndValue title={'Wraith Base Extra Bones'} value={`${notateNumber(wraith?.extraBones, 'MultiplierInfo')}x`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="grimoire.jsx" />
    </Stack>
    <Divider sx={{
      mb: 3,
      mt: {
        xs: 2,
        md: 0
      }
    }} data-sentry-element="Divider" data-sentry-source-file="grimoire.jsx" />
    <Stack direction={'row'} gap={2} flexWrap={'wrap'} alignItems={'center'} data-sentry-element="Stack" data-sentry-source-file="grimoire.jsx">
      {upgrades?.map(({
        name,
        cost,
        description,
        bonus,
        monsterProgress,
        boneType,
        unlockLevel,
        level,
        unlocked
      }, index) => {
        if (name === 'Ripped_Page') return null;
        const desc = description.replace('{', commaNotation(bonus)).replace('}', notateNumber(1 + bonus / 100, 'MultiplierInfo'));
        return <Card key={name + index}>
            <CardContent sx={{
            display: 'flex',
            flexDirection: 'column',
            width: 370,
            minHeight: 250,
            height: '100%',
            opacity: unlocked ? 1 : .5
          }}>
              <Stack direction={'row'} gap={2} flexWrap={'wrap'} alignItems={'center'}>
                <img style={{
                width: 32,
                height: 32
              }} src={`${prefix}data/GrimoireUpg${index}.png`} />
                <Typography>{cleanUnderscore(name.replace(/[船般航舞製]/, '').replace('(Tap_for_more_info)', '').replace('(#)', ''))} ({level})</Typography>
              </Stack>
              <Divider sx={{
              my: 1
            }} />
              <Typography>{cleanUnderscore(desc.replace('$', ` ${cleanUnderscore(monsterProgress)}`).replace('.00', ''))}</Typography>
              <Divider sx={{
              my: 1
            }} />
              <Stack direction={'row'} gap={1} flexWrap={'wrap'} alignItems={'center'}>
                <img style={{
                objectPosition: '0 -6px'
              }} src={`${prefix}data/Bone${boneType}_x1.png`} />
                <Typography>Cost: {notateNumber(bones?.[boneType] || 0)} / {notateNumber(cost, 'Big')}</Typography>
              </Stack>
              <Divider sx={{
              my: 1
            }} />
              <Typography>Unlocks at: {commaNotation(unlockLevel)} levels</Typography>
            </CardContent>
          </Card>;
      })}
    </Stack>
  </>;
};
export default Grimoire;